<template>
  <transition name="fade">
    <div class="login-page">
      <div v-title :data-title="this.$route.name"></div>
      <header class="login-header">
        <div class="container">
          <div class="logo">
            <span></span>
            <h3></h3>
          </div>
        </div>
      </header>
      <main class="login-main">
        <div class="container">
          <el-form 
            :model="loginForm" 
            :rules="loginRules" 
            ref="loginForm" 
            class="login-form" 
            :label-width="labelWidth">
            <h2 class="form-title">.</h2>
            <el-form-item label="" prop="email">
              <el-input v-model.string="loginForm.email" :autofocus="true" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label=" " prop="passwd">
              <el-input type="password" v-model="loginForm.passwd" :autofocus="true" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="confirmPass">
              <el-input type="password" v-model="loginForm.confirmPass" :autofocus="true" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="authcode" :autofocus="true" class="authcode">
              <el-input type="text" v-model="loginForm.authcode" placeholder=""></el-input>
              <canvas id="canvasCode" width="80px" height="30px" class="canvas-code" @click="drawCode"></canvas>
            </el-form-item>
            <el-form-item prop="agreement" label-width="0" class="agreement">
              <el-checkbox-group v-model="loginForm.agreement">
                <el-checkbox name="agreement" label="" v-model="checked"></el-checkbox>
                <span class="show" @click="dialogVisible = true"></span>
              </el-checkbox-group>
            </el-form-item>
            <el-form-item label-width="0" class="submit">
              <el-button 
                type="primary" 
                :loading="logging"
                style="width: 100%" 
                @click="submitForm">
                 
              </el-button>
            </el-form-item>
            <el-form-item label-width="0" class="link">
              <span style="margin: 0 10px">
                
                <router-link to="/login" class="login"></router-link>
              </span>
            </el-form-item>
          </el-form>
          <el-dialog title="" :visible.sync="dialogVisible">
            <div class="agreement-content">
              <h4></h4>
              <p> ""www.yueshijue.com""""</p>
              <h5> 1 </h5>
              <p><strong>1.1</strong> </p>
              <p><strong>1.2</strong></p>
              <p><strong>1.3</strong>18</p>
              <p><strong>1.4</strong></p>
              <h5> 2 </h5>
              <p><strong>2.1</strong></p>
              <p><strong>2.2</strong>12 </p>
              <h5> 3 </h5>
              <p><strong>3.1</strong> </p>
              <p><strong>3.2</strong>/ </p>
              <p><strong>3.3</strong> </p>
              <p><strong>3.4</strong><strong></strong></p>
              <p><strong>3.5</strong><strong></strong></p>
              <p><strong>3.6</strong><strong></strong></p>
              <h5> 4 </h5>
              <p> </p>
              <p><strong>1</strong> </p>
              <p><strong>2</strong></p>
              <p><strong>3</strong> </p>
              <p><strong>4</strong></p>
              <p><strong>5</strong></p>
              <p><strong>6</strong></p>
              <p><strong>7</strong></p>
              <p><strong>8</strong></p>
              <p><strong>9</strong></p>
              <p> </p>
              <p><strong></strong></p>
              <p><strong></strong></p>
              <h5> 5 </h5>
              <p> </p>
              <p> """"</p>
              <h5> 6 </h5>
              <p><strong>6.1</strong><span></span> </p>
              <p><strong>6.2</strong><strong></strong> </p>
              <p> <strong>6.3</strong><strong></strong> </p>
              <h5> 7 </h5>
              <p><strong>7.1</strong> </p>
              <p><strong>7.2</strong></p>
              <p><strong>1</strong> </p>
              <p><strong>2</strong></p>
              <p><strong>3</strong></p>
              <p><strong>4</strong></p>
              <h5> 8 </h5>
              <p><strong>8.1</strong><strong></strong> </p>
              <p><strong>8.2</strong><strong>2011</strong> </p>
              <p> <strong>8.3</strong><strong></strong> </p>
              <p><strong>8.4</strong><strong>,,,</strong> </p>
              <p><strong>8.5</strong><strong>,</strong> </p>
              <p><strong>8.6</strong> </p>
              <h5> 9 </h5>
              <p><strong>,""""</strong> </p>
              <p> <strong>,</strong> </p>
              <p><strong></strong></p>
              <p><strong></strong></p>
              <h5> 10 </h5>  <strong><em></em></strong> 
              <h5> 11 </h5>   
              <h5> 12  </h5>
              <p><strong>12.1</strong></p>
              <p><strong>12.2</strong> </p>
              <p><strong>12.3</strong><span></span></p>
              <p><strong>12.4</strong><span>""</span> </p>
            </div>
            <span slot="footer" class="dialog-footer">
              <el-button 
                type="danger" 
                style="width: 100%" 
                @click="dialogVisible = false">
                 
              </el-button>
            </span>
          </el-dialog> 
        </div>
      </main>
      <footer class="login-footer">
        <div class="container">
          <p>Copyright  2017 yueshijue.com</p>
        </div>
      </footer>
    </div>
  </transition>
</template>
<script>
  import Utils from '@/assets/js/utils'
  import { requestRegist, requestLogin, accountFind } from '@/api'
  export default {
    data() {
      const validateEmail = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else {
          let params = {
            account: value
          }
          accountFind(params).then(res => {
            if(res.data.code === '0001') {
              if(!res.data.result.finded) {
                callback()
              } else {
                callback(new Error(''))
              }
            } else {
              this.$message.error(res.data.message)
            }
          })
        }
      }
      const validateConfirmPass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.loginForm.passwd) {
           callback(new Error(''))
        } else {
          callback()
        }
      }
      const validateCode = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else if (value.toUpperCase() !== this.authCode.toUpperCase()) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        labelWidth: '90px',
        dialogVisible: false,
        logging: false,
        checked: true,
        authCode: '',
        loginForm: {
          email: '',
          passwd: '',
          confirmPass: '',
          authcode: '',
          agreement: [],
        },
        loginRules: {
          email: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: 'blur' },
            { validator: validateEmail, trigger: 'blur' },
          ],
          passwd: [
            { required: true, message: '', trigger: 'blur' },
            { min: 8, max: 20, message: ' 8  20 ', trigger: 'blur'}
          ],
          confirmPass: [
            { required: true, validator: validateConfirmPass, trigger: 'blur' }
          ],
          authcode: [
            { required: true, validator: validateCode, trigger: 'blur'}
          ],
          agreement: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ]
        }
      };
    },
    methods: {
      drawCode () {
        this.authCode = Utils.canvasCode('canvasCode')
      },
      submitForm() {
        this.$refs.loginForm.validate((valid) => {
          if (valid && !this.logging) {
            this.logging = true;
            let data = {
              email: this.loginForm.email,
              passwd:  this.$Md5.hex_md5(this.loginForm.passwd),
              passwd2: this.$Md5.hex_md5(this.loginForm.confirmPass),
            }
            requestRegist(data).then(res => {
              if (res.data.code === '0001') {
                if(res.data.result.redirectUrl) {
                  window.location.href = res.data.result.redirectUrl;
                } else {
                  this.$message.success('')
                  this.loginSubmit()
                }
              } else {
                this.$message.error(res.data.message)
                this.drawCode()
              }
              this.logging = false
            })
          }
        })
      },
      loginSubmit() {
        let data = {
          username: this.loginForm.email,
          password: this.$Md5.hex_md5(this.loginForm.passwd),
        }
        requestLogin(data).then(res => {
          if(res.data.code === '0001') {
            let userId = res.data.result.userInfo.userId
            Utils.setCookie('userId', userId)
            this.$message.success('')
            this.$router.push({ path: '/account/infocenter' })
          } else {
            this.$message.error(res.data.message)
          }
        }).catch(err => {
          console.log(err)
          this.$catchError(err)
        })
      }
    },
    mounted () {
      this.drawCode()
    }
  }
</script>
<style scoped lang="scss">
  .login-main {
    .login-form {
      padding-top: 20px;
      padding-bottom: 10px;
    }
  }
  .agreement-content {
    height: 350px; 
    overflow-y: auto;
    @media (min-width: 1900px) {
      height: 500px;
    }
    h4 {
      font-weight: bold;
      margin-bottom: 15px;
    }
    h5 {
      margin: 5px 0;
    }
    p {
      margin: 10px 0;
      line-height: 1.3;
    }
  }
</style>



