<template>
	<aside class="sidebar transition" :class="{'hide': sidebarStatus}">
    <div class="sidebar-wrap">
      <div class="sidebar-header">
        <a href="/" class="brand ellipsis" :title="title">
          <i class="fa fa-flag fa-lg"></i>
          <strong>{{title}}</strong>
        </a>
      </div>
      <div class="divide-line"></div>
  		<el-menu 
        router 
        theme="dark" 
        mode="vertical" 
        :default-active="$route.path" 
        :unique-opened="true" 
        :collapse="isCollapse"
        class="el-menu-vertical-demo">
        <el-submenu 
          :index="index+''" 
          v-for="(menu, index) in routers" 
          :key="menu.id">
          <template slot="title">
            <i class="fa fa-lg" :class="menu.icon"></i>
            <span slot="title">{{ menu.name }}</span>
          </template>
          <el-menu-item 
            :index="submenu.link" 
            v-for="submenu in menu.children" 
            :key="submenu.id">
          	<router-link :to="submenu.link">{{ submenu.name }}</router-link>
          </el-menu-item>
            <!-- <el-submenu :index="submenu.index" v-for="submenu in menu.children" :key="submenu.id">
              <template slot="title">{{ submenu.name }}</template>
              <el-menu-item :index="submenu.index" v-for="item in submenu.children" :key="submenu.id">{{item.name}}</el-menu-item>
            </el-submenu> -->
        </el-submenu>
      </el-menu>
    </div>
	</aside>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      title: '',
      isCollapse: false,
      routers: [
        {
          name: '-',
          icon: 'fa-paw',
          children: [
            {name: '', link: '/provider/ware/brandManage'},
            {name: '', link: '/provider/ware/new/base'},
            {name: '', link: '/provider/ware/wareManage'},
            {name: '', link: '/provider/ware/storageManage'},
            {name: '', link: '/provider/ware/shelfManage'},
          ]
        },
        {
          name: '-',
          icon: 'fa-paw',
          children: [
            // {name: '', link: '/provider/order/confirmed', index: '/provider/order/confirmed'},
            {name: '', link: '/provider/order/payment', index: '/provider/order/payment'},
            {name: '', link: '/provider/order/inService', index: '/provider/order/inService'},
            {name: '', link: '/provider/order/settlement', index: '/provider/order/settlement'},
            {name: '', link: '/provider/order/history', index: '/provider/order/history'},
            {name: '', link: '/provider/order/problem', index: '/provider/order/problem'},
          ]
        },
        {
          name: '-',
          icon: 'fa-desktop',
          children: [
            {name: '', link: '/admin/system/propertyManage', index: '/admin/system/propertyManage'},
            {name: '', link: '/admin/system/templateManage', index: '/admin/system/templateManage'},
            {name: '', link: '/admin/system/categoryManage', index: '/admin/system/categoryManage'},
            // {name: '', link: '/admin/system/marketingStrategy', index: '/admin/system/marketingStrategy'},
            {name: '', link: '/admin/system/settlementStrategy', index: '/admin/system/settlementStrategy'},
            {name: '', link: '/admin/system/retreatStrategy/retreatStrategyList', index: '/admin/system/retreatStrategy/retreatStrategyList'},
          ]
        },
        {
          name: '-',
          icon: 'fa-desktop',
          children: [
            {name: '', link: '/admin/ware/verifyList', index: '/admin/ware/verifyList'},
            {name: '', link: '/admin/ware/shelfManage', index: '/admin/ware/shelfManage'},
            {name: '', link: '/admin/ware/priceManage', index: '/admin/ware/priceManage'},
            {name: '', link: '/admin/ware/manage', index: '/admin/ware/manage'},
          ]
        },
        {
          name: '-',
          icon: 'fa-desktop',
          children: [
            // {name: '', link: '/admin/order/confirmed', index: '/admin/order/confirmed'},
            {name: '', link: '/admin/order/payment', index: '/admin/order/payment'},
            {name: '', link: '/admin/order/inService', index: '/admin/order/inService'},
            {name: '', link: '/admin/order/settlement', index: '/admin/order/settlement'},
            {name: '', link: '/admin/order/history', index: '/admin/order/history'},
            {name: '', link: '/admin/order/problem', index: '/admin/order/problem'},
          ]
        },
        // {
        //  name: '-',
        //  icon: 'fa-desktop',
        //   children: [
        //    {name: '', link: '/admin/customer/query', index: '/admin/customer/query'},
        //   ]
        // },
        {
          name: '-',
          icon: 'fa-sitemap',
          children: [
            {name: '', link: '/distributor/order/complete', index: '/distributor/order/complete'},
            {name: '', link: '/distributor/order/payment', index: '/distributor/order/payment'},
            {name: '', link: '/distributor/order/inService', index: '/distributor/order/inService'},
            // {name: '', link: '/distributor/order/complaint', index: '/distributor/order/complaint'},
            {name: '', link: '/distributor/order/history', index: '/distributor/order/history'},
          ]
        },
        {
          name: '-',
          icon: 'fa-sitemap',
          children: [
            {name: '', link: '/distributor/bill/expenditure', index: '/distributor/bill/expenditure'},
            {name: '', link: '/distributor/bill/income', index: '/distributor/bill/income'}
          ]
        }
      ],
    }
  },
  computed: {
    ...mapGetters([
      'sidebarStatus'
    ]),
    // ...mapGetters({
    //   routers: 'userPermission'
    // }),
  },
}
</script>
<style scoped lang="scss">
	.sidebar {
		position: fixed;
		top: 0;
    left: 0;
		width: 240px;
		height: 100%;
    overflow: hidden;
		background-color: #324157;
	}
  .sidebar.hide {
    left: -240px;
  }
  .sidebar-wrap {
    position: absolute;
    width: 260px;
    height: 100%;
    overflow-y: scroll;
  }
  .sidebar-header {
    padding: 20px 15px;
    text-align: left;
    .brand {
      margin: 10px 0;
      font-size: 16px;
      line-height: 1.1;
      color: #DFE4ED;
    }
  }
  .divide-line {
    width: 100%;
    height: 2px;
    overflow: hidden;
    font-size: 0;
    background-color: #233b4c;
  }
  .el-submenu {
    text-align: left;
    .el-menu-item {
      padding-left: 10px
    }
  }
	.el-menu-item {
    text-indent: 5px;
		a {
			display: block;
			text-decoration: none;
			color: #bfcbd9;
		}
		.router-link-active {
			color: #fff;
		}
	}
	.el-submenu .fa {
		vertical-align: baseline;
    margin-right: 10px;
	}
</style>



